body {
  font-family: "Open Sans", sans-serif;
}
#popular a {
  text-decoration: none;
  color: #fff;
  font-size: 0.8rem;
  font-weight: bold;
}
#popular a:hover {
  text-decoration: none;
  color: #ccc;
}

#trending a {
  text-decoration: none;
  color: #fff;
  font-size: 0.8rem;
  font-weight: bold;
}
#trending a:hover {
  text-decoration: none;
  color: #ccc;
}

.movie-grid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 8px;
  padding-left: 20px;
}

.dark-overlay:hover {
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}

.card:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 150px;
  background: rgba(0, 0, 0, 0.3);
  content: "";
  height: 100%;
}

.card:after:hover {
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  -webkit-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}

.card-img-overlay {
  z-index: 1 !important;
}

.carousel-item:after {
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;
  content: "";
}

.carousel-indicator {
  list-style-type: circle !important;
}

/* Media queries */
@media (min-width: 700px) {
  .movie-grid {
    grid-template-columns: repeat(4, 1fr);
  }

  .card:after {
    position: absolute;
    top: 0;
    left: 0;
    width: 150px;
    background: rgba(0, 0, 0, 0.3);
    content: "";
    height: 100%;
  }

  .display-4 {
    font-size: 2rem !important;
  }

  .lead {
    font-size: 0.7rem !important;
  }
}

@media (max-width: 500px) {
  .movie-grid {
    grid-template-columns: 1fr;
  }
}

